import React, { useState } from 'react';
import { UserContext } from './components/MyContext';
import SearchBox from './components/searchBox'
import ResultBox from './components/resultBox'
import GeoLocalisation from './components/geolocalisation'
import 'bootstrap/dist/css/bootstrap.css'

function App() {

	/* requête du nom de la ville */
	const [query, setQuery] = useState ('')

	/*stocke le résultat de la requête*/
	const [result, setResult] = useState ({})	

	const contextValue = {
		query,setQuery,
		result,setResult			
	  };	

  return (
    <div className={
		(typeof result.weather !="undefined") 
		?  result.weather[0].main
		:'intro'}>		

		<UserContext.Provider value={contextValue}>
     	<main>	

			{/* Zone pour le bouton "position" et l'input de recherche par nom de commune*/}
			<div className='container'>
				<div className='row'>
					<div className="col-3">
						<GeoLocalisation></GeoLocalisation>
					</div>
					<div className="col-9">
						<SearchBox></SearchBox>							
					</div>
				</div>		
			 </div>
					
			{/* Affiche la météo si la requête marche*/}
			{(typeof result.main !="undefined") 
				? (<ResultBox></ResultBox>)
				: ('') 
			}
			
			{/* Affiche une erreur si la requête échoue*/}
			<div className="textErreur">
				{result.message &&
					<div>				
						La commune n'existe pas ou elle est mal orthographiée ...
					</div>
				}
			</div>		
			
     	</main>
	  	</UserContext.Provider>
    </div>  
  )
}

export default App;