import React, { useContext } from 'react';
import { UserContext } from './MyContext';

const api = {
	key : process.env.REACT_APP_TOKEN,
	base :"https://api.openweathermap.org/data/2.5/"
  }

function SearchBox () {  
    
    const {query}=useContext(UserContext)
    const {setQuery}=useContext(UserContext)   
    const {setResult}=useContext(UserContext)
    
    /*recherche fait dans l'input*/
	const search = event =>{
        
		if (event.key === "Enter"){
			fetch(`${api.base}weather?q=${query}&units=metric&appid=${api.key}&lang=fr`)
			.then(async (dataJson) => {							
				setResult(await dataJson.json()	)					
				setQuery('')                             	
			})				
			.catch(console.error)
		}			
	}	
    
    return(        

        <div className="search-box" >
                <input
                    id="query"
                    type="text"
                    className="search-bar"
                    placeholder=" Recherche..."
                    onChange={event => setQuery(event.target.value)}
                    value={query}
                    onKeyPress={search}
                />     
        </div>
    )      
}

export default SearchBox